.list{
    background-color: #bf07ac;
    color: #fff;
    width: 300px;
    height: 50px;
    margin: 20px;
    border-radius: 5px;
}
.list p{
    position: relative;
    padding: 10px;
}
.list p span{
    position: absolute;
    right: 10px;
}
.faicon{
    margin: 0 5px;
    font-size: 15px;
}